
import {
	mapMutations
} from 'vuex';
export default {
	onLaunch: function() {
		console.log('App Launch')
		let userInfo = uni.getStorageSync('userInfo') || '';
		if(userInfo && userInfo.id){
			//更新登陆状态
			uni.getStorage({
				key: 'userInfo',
				success: (res) => {
					if(res && res.data)
					    this.login(res.data);
				}
			});
		}
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
	methods: {
		...mapMutations(['login'])
	},
}
